.content__search {
    .search__box {
        position: relative;
        width: 300px;
        height: 60px;
        &-input {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            padding: 10px 65px 10px 20px;
            border-radius: 3px;
            font-size: 18px;
            border-radius: 50px;
        }
        &-button {
            position: absolute;
            top: 0;
            right: 0;
            width: 60px;
            height: 100%;
            background: #7690da;
            z-index: 1;
            cursor: pointer;
            right: 0px;
            border-radius: 50%;
            &:hover {
                background: #708bd2;
            }
        }
        &-icon {
            width: 30px;
            height: 30px;
        }
    }
}
